  height: 134px;
  margin: 0 auto;
}

.card__title {
  text-align: left;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  display: flex;
}

.card__code {
  text-align: left;
  color: #616070;
  margin: 0 0 16px;
  font-weight: 400;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
